.item {
  display: flex;
  padding: 1rem;
  gap: 1rem;
  transition: all 0.3s ease 0s;
  * {
    border-radius: 7px;
  }
  &:not(:last-child) {
    margin-bottom: 2rem;
  }
}
.image {
  flex: 0 0 250px;
  height: 125px;
  background-color: gainsboro;
  img {
    height: 100%;
    width: 100%;
  }
}

.body {
  flex: 1;
  display: flex;
  flex-direction: column;
  h3 {
    margin-top: 0;
    height: 20px;
    max-width: 600px;
  }
  p {
    height: 50px;
  }
  h3,
  p {
    background-color: gainsboro;
  }
}

.data {
  display: flex;
  gap: 2rem;
  justify-content: flex-end;
  flex: 1;
  align-items: flex-end;
  div {
    height: 20px;
    width: 150px;
    background-color: gainsboro;
  }
}
