.newsPage {
  padding: 1rem;
}
.item {
  display: flex;
  padding: 1rem;
  gap: 1rem;
  transition: all 0.3s ease 0s;
  &:hover {
    h3 {
      text-decoration: underline;
    }
  }
  &:not(:last-child) {
    margin-bottom: 2rem;
  }
}
.image {
  flex: 0 0 250px;
  height: 125px;
  img {
    height: 100%;
    width: 100%;
  }
}

.body {
  flex: 1;
  display: flex;
  flex-direction: column;
  h3 {
    margin-top: 0;
  }
}

.data {
  display: flex;
  gap: 2rem;
  justify-content: flex-end;
  flex: 1;
  align-items: flex-end;
}
.error {
  margin-top: 3rem;
  text-align: center;
  font-size: 25px;
}

.category {
  margin: 2rem 0;
  font-size: 20px;
  form {
    display: flex;
    gap: 3em;
    align-items: center;
    justify-content: center;
  }
}

.radio {
  display: flex;
  gap: 1rem;
  align-items: center;
  cursor: pointer;
  padding: 0.5rem;
  input {
    display: none;
    &:checked {
      ~ .mock {
        &::after {
          background-color: rgb(255, 0, 140);
        }
      }
    }
  }
}

.mock {
  border-radius: 100%;
  height: 20px;
  width: 20px;
  background: rgb(0, 140, 255);
  position: relative;

  &::after {
    position: absolute;
    content: '';
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 10px;
    width: 10px;
    border-radius: 50%;
    transition: all 0.3s ease 0s;
  }
}
